button:not(.input-button):not(.switch-button) {
	@include general-button;

	&:hover:not([disabled]):not(.disabled) {
		@include general-button-hover;
	}
	&:active:not([disabled]):not(.disabled) {
		@include general-button-active;

		// Exception for Internet Explorer 10 & 11
		@include general-button-active-internet-explorer;
	}
	&[disabled] {
		@include general-button-disabled;
	}
}
