// The width breakpoint from which we'll fix the plectrum links
// This could be considered anything larger than a phone
$bp-plectrum-fixation: $main-max-width + 2 * $plectrum-width + calc-rem(20px);

// We'll consider everything wider than an iPad Pro a desktop
$bp-desktop: $ipadpro-width + calc-rem(1px);

// Every viewport that is wide enough to have the plectrum links fixed in the margin
@mixin media-query-tablet {
	@media only screen and (min-width:$bp-plectrum-fixation) {
		@content;
	}
}
// Every viewport that is wide enough to have the plectrum links fixed in the margin
@mixin media-query-tablet-and($query-prop, $query-val) {
	@media only screen and (min-width:$bp-plectrum-fixation) and ($query-prop: $query-val) {
		@content;
	}
}

@mixin media-query-desktop() {
	@media
		// every device wider than an ipadpro with a viewport wider than the max page width
		only screen
			and (min-device-width: #{$bp-desktop})
			and (min-width: $page-max-width)
		,
		// or every device wider than a landscaped iphoneX and less high than a landscaped ipad
		// (i.e. landscaped tablets)
		only screen
			and (min-device-width: #{$iphoneX-height + calc-rem(1px)})
			and (max-device-height: #{$ipad-width})
	{
				@content;
	}
}
@mixin media-query-desktop-and($query-prop, $query-val) {
	@media
		// every device wider than an ipadpro with a viewport wider than the max page width
		only screen and (min-device-width: #{$bp-desktop}) and (min-width: $page-max-width) and ($query-prop: $query-val),
		// or every device wider than a landscaped iphoneX and less high than a landscaped ipad
		// (i.e. landscaped tablets)
		only screen and (min-device-width: #{$iphoneX-height + calc-rem(1px)}) and (max-device-height: #{$ipad-width}) and ($query-prop: $query-val) {
			@content;
	}
}
