.switch {
	& > div {
		display: flex;
	}

	&-button {
		flex: 1;
		display: inline-block;
		position: relative;
		@include general-button;
		text-decoration: none;
		color: $orange-300;
		font-weight: normal;

		&:visited {
			color: $orange-300;
		}

		&:not(:first-child) {
			border-left: none;
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
		}
		&:not(:last-child) {
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
		}

		// Make sure the focus outline is not cut off by another button
		&:focus {
			z-index: 10;
		}

		&:hover:not([disabled]):not(.disabled):not(.selected) {
			@include general-button-hover;
		}
		&:active:not(.selected):not([disabled]):not(.disabled) {
			@include general-button-active;
		}
		&.selected {
			@include general-button-selected;

			// Add a separator line so the buttons don't become
			// one big block when hovering a non-checked button
			// next to a checked button
			&:after {
				content: '';
				display: block;
				width: calc-rem(3px);
				height: 100%;
				position: absolute;
				background-color: white;
				left: 100%;
				top: 0;
			}
			&:last-of-type {
				&:after {
					left: calc-rem(-3px);
				}
			}
		}
		&[disabled] {
			@include general-button-disabled;
		}
	}
}

// Exception for Internet Explorer 10 & 11
button.switch-button:active:not(.selected):not([disabled]):not(.disabled) {
	@include general-button-active-internet-explorer;
}
