// See https://a11yproject.com/posts/how-to-hide-content/
@mixin hidden-accessible {
    position: absolute !important;
    height: 1px;
    width: 1px;
    overflow: hidden;
    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
    clip: rect(1px, 1px, 1px, 1px);
    white-space: nowrap; /* added line */
}
.hidden-accessible {
	@include hidden-accessible;
}

@mixin general-button {
	margin-bottom: 1rem;
	background: white;
	border: calc-rem(3px) solid $orange-300;
	display: block;
	width: 100%;
	color: $orange-300;
	font-family: TheMix, Arial, Helvetica, sans-serif;
	font-size: calc-rem(18px);
	line-height: calc-rem(36px);
	padding: calc-rem(1px) calc-rem(11px);
	border-radius: calc-rem(8px);
	text-align: center;
	cursor: pointer;

	// A dark button
	&.dark {
		border-color: $grey-500;
		color: $grey-200;
	}
}

@mixin general-button-hover {
	color: $orange-100;
	border-color: $orange-100;
	text-decoration: none;

	// A dark button
	&.dark {
		border-color: $grey-300;
		color: $grey-100;
	}
}

@mixin general-button-active {
	// Use the same styles as a hovered button
	@include general-button-hover;
	// Move the text a little to make a button show pressed
	padding: calc-rem(2px) calc-rem(10px) calc-rem(0px) calc-rem(12px);
}

@mixin general-button-active-internet-explorer {
	// Remove the padding for IE10 & IE11 because IE does it's own button pressed state
	// and adding ours makes elements below the button move (weirddddd)
	@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
		padding: calc-rem(1px) calc-rem(11px);
	}
}

@mixin general-button-selected {
	color: white;
	background-color: $orange-300;
	text-decoration: none;

	&:hover {
		color: white;
	}

	// A dark button
	&.dark {
		border-color: $grey-300;
		color: $grey-100;
	}
}

@mixin general-button-disabled {
	opacity: 0.4;
	cursor: default;
	border-color: $grey-500;
	color: $color-text-body;
}

$focus-line-width: calc-rem(1px);
$focus-line-color: $orange-300;
$focus-line-style: solid;
$focus-line-offset: calc-rem(5px);

@mixin focus-outline {
	outline: $focus-line-width $focus-line-style $focus-line-color;
	outline-offset: $focus-line-offset;
}

@mixin remove-focus-outline {
	outline: none;
}

@mixin focus-border {
	outline: none;
	border: $focus-line-width $focus-line-style $focus-line-color;
}

@mixin remove-focus-border {
	border: none;
}
