h1, h2, h3, h4, h5, h6 {
	font-family: TheMix, Arial, Helvetica, sans-serif;
	font-weight: normal;
	line-height: 1.5;
	margin-bottom: 0.5rem;
	color: $color-text-title;
}

h1 {
	font-size: calc-rem(21px);
	margin-bottom: calc-rem(18px);

	&.index-page {
		text-align: center;
	}
}
h2 {
	font-size: calc-rem(19px);
	margin-bottom: calc-rem(12px);
}

.heading-with-ruler {
	&-wrap {
		position: relative;
		margin-bottom: calc-rem(12px);
		text-align: center;

		hr {
			position: absolute;
			display: block;
			width: 100%;
			top: 50%;
			transform: translateY(-50%);
			border: none;
			border-top: calc-rem(1px) solid $color-ruler;
			margin: 0;
			z-index: 1;
		}
	}

	&-text {
		display: inline-block;
		background-color: white;
		font-family: Arial, Helvetica, sans-serif;
		font-size: calc-rem(16px);
		margin-bottom: 0;
		padding: 0 calc-rem(5px);
		position: relative;
		z-index: 2;
	}
}
