$checkbox-size: calc-rem(20px);
$checkbox-position-y-offset: calc-rem(-2px);
$checkbox-border-radius: 15%; // 3px for 20px checkboxes

fieldset.checkboxes {
	margin-bottom: calc-rem(16px);
}

input[type="checkbox"] {
	@include hidden-accessible;

	+ label {
		padding-left: calc-rem(32px);
		display: inline-block;
		position: relative;
		margin-bottom: 0.5rem;
		min-height: $checkbox-size;

		// The box of the checkbox
		&:before {
			content: ' ';
			display: block;
			width: $checkbox-size;
			height: $checkbox-size;
			border: calc-rem(1px) solid $grey-600;
			border-radius: $checkbox-border-radius;
			position: absolute;
			left: 0;
			top: calc(50% + #{$checkbox-position-y-offset});
			transform: translateY(-50%);
		}
	}

	&:checked {
		+ label {
			// The box of the checkbox
			&:before {
				border-color: $orange-300;
			}

			// The check of the checkbox
			&:after {
				content: ' ';
				display: block;
				position: absolute;
				left: calc-rem(2px);
				top: calc-rem(7px);
				width: calc-rem(7px);
				height: calc-rem(13px);
				border-bottom: calc-rem(3px) solid $orange-300;
				border-right: calc-rem(3px) solid $orange-300;
				top: calc(50% - #{calc-rem(5px)});
				transform: rotate(45deg) translateY(-50%);
			}
		}
	}

	// Focus
	// Since we created a custom checkbox inside the label
	// we must also create a custom focus outline
	&:focus + label {
		.focus-ring {
			content: ' ';
			display: block;
			position: absolute;
			width: $checkbox-size + 2*$focus-line-width + 2*$focus-line-offset;
			height: $checkbox-size + 2*$focus-line-width + 2*$focus-line-offset;
			left: - $focus-line-width - $focus-line-offset;
			border-radius: $checkbox-border-radius;
			top: calc(50% + #{$checkbox-position-y-offset});
			transform: translateY(-50%);

			@include focus-border;
		}
	}
	&:focus:not(:focus-visible) + label {
		.focus-ring {
			display: none;
		}
	}
	&:focus:not(.focus-visible) + label {
		.focus-ring {
			display: none;
		}
	}
	// Note: Since the actual inputs are hidden and thus not clickable
	// we do not have to add styling for :focus-visible
}
