a {
	color: $orange-300;
	text-decoration: none;
	font-weight: bold;

	&:hover,
	&:hover:visited {
		color: $orange-100;
	}
	&:visited {
		color: $orange-300;
	}

	&.button {
		@include general-button;
		text-decoration: none;
		font-weight: normal;

		&:hover {
			@include general-button-hover;
		}
		&:active {
			@include general-button-active;
		}
	}
}

.cta-wrap {
	&.right-align {
		text-align: right;
	}
	&.center-align {
		text-align: center;
	}
}
a.cta {
	// Make it an inline block, so we can add margin
	display: inline-block;
	margin: calc-rem(9px) auto;
	text-decoration: none;
}

// Mobile offsets are from the bottom of main element
// (i.e. right above the logo)
$plectrum-back-bottom-offset: -$plectrum-height / 2;
$plectrum-help-bottom-offset: $plectrum-back-bottom-offset + calc-rem(87px);

a.help,
a.back {
	display: inline-block;
	height: $plectrum-height;
	width: $plectrum-width;
	position: absolute;
	overflow: hidden;

	// Tablet portrait
	@include media-query-tablet {
		position: fixed;
	}
}

a.help {
	right: 0;
	bottom: $plectrum-help-bottom-offset;

	// Tablet portrait
	@include media-query-tablet {
		top: auto;
		bottom: $plectrum-help-bottom-offset + $header-height;
	}
	// If the browser's height is too small to fix the plectrums on the bottom
	@include media-query-tablet-and(max-height, $plectrum-back-bottom-offset + $header-height + $plectrum-height + calc-rem(50px) + $plectrum-help-bottom-offset - $plectrum-back-bottom-offset) {
		top: calc-rem(50px);
		bottom: auto;
	}

	//== Desktop
	@include media-query-desktop {
		right: calc((100% - #{$page-max-width}) / 2);
		top: auto;
		bottom: $plectrum-help-bottom-offset + $header-height + $body-padding-bottom-desktop;
	}
	// If the browser's height is too small to fix the plectrums on the bottom
	@include media-query-desktop-and(max-height, $plectrum-help-bottom-offset + $header-height + $body-padding-bottom-desktop + $plectrum-height + calc-rem(50px)) {
		top: calc-rem(50px);
		bottom: auto;
	}

	svg {
		g{
			fill: $orange-300;
			stroke: $orange-300;
		}

		text {
			font-family: TheMix, Courier, serif !important;
			font-size: calc-rem(36px);
			font-weight: 500;
		}
	}

	&:hover {
		svg {
			g {
				fill: $orange-100;
				stroke: $orange-100;
			}
		}
	}
}

a.back {
	bottom: $plectrum-back-bottom-offset;
	left: 0;

	// Tablet portrait
	@include media-query-tablet {
		top: auto;
		bottom: $plectrum-back-bottom-offset + $header-height;
	}
	// If the browser's height is too small to fix the plectrums on the bottom
	@include media-query-tablet-and(max-height, $plectrum-back-bottom-offset + $header-height + $plectrum-height + calc-rem(50px) + $plectrum-help-bottom-offset - $plectrum-back-bottom-offset) {
		top: calc-rem(50px) + $plectrum-help-bottom-offset - $plectrum-back-bottom-offset;
		bottom: auto;
	}

	//== Desktop
	@include media-query-desktop {
		left: calc((100% - #{$page-max-width}) / 2);
		top: auto;
		bottom: $plectrum-back-bottom-offset + $header-height + $body-padding-bottom-desktop;
	}
	// If the browser's height is too small to fix the plectrums on the bottom
	@include media-query-desktop-and(max-height, $plectrum-back-bottom-offset + $header-height + $body-padding-bottom-desktop + $plectrum-height + calc-rem(50px) + $plectrum-help-bottom-offset - $plectrum-back-bottom-offset) {
		top: calc-rem(50px) + $plectrum-help-bottom-offset - $plectrum-back-bottom-offset;
		bottom: auto;
	}

	svg {
		g{
			fill: $grey-500;
			stroke: $grey-500;
		}
	}

	&:hover {
		svg {
			g {
				fill: $grey-300;
				stroke: $grey-300;
			}
		}
	}

}
