input[type="text"],
input[type="password"],
input[type="email"] {
	position: relative;
	padding: calc-rem(13px) calc-rem(15px) calc-rem(9px);
	width: 100%;
	border: calc-rem(1px) solid $grey-600;
	font-size: calc-rem(14px);
	margin-bottom: 1rem;

	&.small {
		max-width: calc-rem(200px);
	}

	&::placeholder {
		color: #b9b9b9;
	}

	&.with-button {
		padding-right: calc-rem(50px);
	}

	+ button.input-button {
		position: absolute;
		width: calc-rem(40px);
		height: calc(100% - #{calc-rem(2px)});
		top: calc-rem(1px);
		right: calc-rem(1px);
		overflow: hidden;
		background: white;
		border: none;
		cursor: pointer;

		.icon {
			width: calc-rem(20px);
			height: calc-rem(20px);
		}
		.text {
			@include hidden-accessible;
		}

		&[aria-pressed="false"] {
			.icon-pressed-true {
				display: none;
			}
			.icon-pressed-false {
				display: inline-block;
			}
		}
		&[aria-pressed="true"] {
			.icon-pressed-true {
				display: inline-block;
			}
			.icon-pressed-false {
				display: none;
			}
		}

		&:hover,
		&:active {
			background-color: $grey-900;
		}
	}

	&.error {
		border-color: $red;
		padding-right: calc-rem(40px);

		@extend .bg-cross-red;
		background-position: center right calc-rem(11px);
		margin-bottom: calc-rem(5px);

		+ .error {
			color: $red;
			margin-bottom: 1rem;
		}
	}

	// Validation styling
	&[type="email"] {
		&:valid {
			border-color: $green;
			padding-right: calc-rem(40px);

			@extend .bg-check-green;
			background-position: center right calc-rem(11px);
		}
	}

	:focus {
		outline-offset: -1px;
	}
	:focus-visible {
		outline-offset: -1px;
	}
}
.js-focus-visible {
	input[type="text"],
	input[type="password"],
	input[type="email"] {
		&.focus-visible {
			outline-offset: -1px;
		}
	}
}


.input-with-button-wrap {
	position: relative;
	margin-bottom: 1rem;

	input[type="text"],
	input[type="password"],
	input[type="email"] {
		margin-bottom: 0;
		padding-right: calc-rem(50px);
	}
}
