html {
	height: 100%;
}

body {
	min-height: 100%;
	font-family: Arial, Helvetica, sans-serif;
	font-size: calc-rem(16px);
	line-height: 1.25;
	color: $color-text-body;
}

.page {
	position: relative;
	width: 100%;
	min-height: 100vh;
	margin: auto;
	padding-bottom: $header-height;

	// Stupid IE
	outline: none !important;
}

header {
	// Needed for IE
	display: block;

	position: absolute;
	bottom: 0;
	left: 0;
}

main {
	padding: calc-rem(52px) calc-rem(39px);
	position: relative;

	// Needed for IE
	display: block;

	&.with-help-btn,
	&.with-back-btn {
		padding-bottom: calc-rem(175px);
	}
}

p {
	margin-bottom: calc-rem(16px);
	line-height: 1.25;
	letter-spacing: calc-rem(0.2px);
}

// Tablet portrait
@include media-query-tablet {
	main {
		max-width: $main-max-width;
		margin: auto;
		padding: calc-rem(170px) 0 0;

		&.with-help-btn,
		&.with-back-btn {
			padding-bottom: calc-rem(39px);
		}
	}
}

@include media-query-tablet-and(max-height, $iphone6-width) {
	main {
		padding-top: calc-rem(52px);
	}
}

@include media-query-desktop {
	body {
		background: $grey-900;
		padding-bottom: $body-padding-bottom-desktop;
	}

	.page {
		background-color: white;
		max-width: $page-max-width;
		width: $page-max-width;
		min-height: calc(100vh - #{$body-padding-bottom-desktop});
		position: relative;

		// width of main will be $main-max-width
		padding: 0 (($page-max-width - $main-max-width) / 2) $header-height;
		box-shadow: 0px 1px 3px $grey-500;
	}

	main {
		padding: calc-rem(88px) 0;
	}
}
