.radio-switch {
	& > div {
		display: flex;
	}

	[type="radio"] {
		@include hidden-accessible;

		+ label {
			flex: 1;
			display: inline-block;
			position: relative;
			@include general-button;

			&:hover {
				@include general-button-hover;
			}
			&:not(:first-of-type) {
				border-left: none;
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;
			}
			&:not(:last-of-type) {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
			}
		}

		&:checked {
			+ label {
				@include general-button-selected;

				// Add a separator line so the buttons don't become
				// one big block when hovering a non-checked button
				// next to a checked button
				&:not(:last-of-type),
				&:last-of-type {
						&:after {
						content: '';
						display: block;
						width: calc-rem(3px);
						height: 100%;
						position: absolute;
						background-color: white;
						left: 100%;
						top: 0;
					}
				}
				&:last-of-type {
					&:after {
						left: calc-rem(-3px);
					}
				}
			}
		}

		&:focus,
		&.focus-visible {
			+ label {
				@include focus-outline;
				z-index: 10;
			}
		}
		&:focus:not(:focus-visible) {
			+ label {
				@include remove-focus-outline;
			}
		}
		&:focus:not(.focus-visible) {
			+ label {
				@include remove-focus-outline;
			}
		}
		&.focus-visible {
			+ label {
				@include focus-outline;
				z-index: 10;
			}
		}
	}
}
